import React from "react";
import "./Footer.scss";

function Footer() {
  return (
    <footer className="bg_component">
      <div className="container ">
        <div className="icon-container">
          <a rel="noopener" href="#">
            <img
              src={require("../../Assets/Images/misc/facebook_icon.svg")}
              alt="facebook_icon"
            />
          </a>
          <a rel="noopener" href="#">
            <img
              src={require("../../Assets/Images/misc/instagram_logo.svg")}
              alt="instagram_icon"
            />
          </a>
        </div>
        <div className="logo">
          <img
            src={require("../../Assets/Images/misc/logo.svg")}
            alt="logo.svg"
          />
          <p>+44 (77) 00 000 000</p>
        </div>
      </div>
    </footer>
  );
}

export default Footer;
