import React from "react";
import "./Home.scss";
import { Link } from "react-router-dom";

class Home extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="home">
        <div className="container main">
          <div className="row">
            <div className="heading col-sm-12 col-md-6">
              <h1>PHOTOGRAPHY</h1>
              <p>
                Professional photographer of landscapes, still photography and
                architecture. All photographs are taken with my Olympus
              </p>
              <Link
                to="/Galery"
                onClick={this.props.action}
                className="btn btn-primary front-weight-bold"
                href="#galery"
              >
                Galery
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
