import React from "react";
import { Link } from "react-router-dom";
import "./Nav.scss";

class Nav extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      home: "",
      galery: "",
      isOpen: false,
    };
  }

  onClickHandler() {
    this.setState({
      isOpen: !this.state.isOpen,
    });
  }

  componentWillReceiveProps() {
    if ((this.props.parentState.galery = "selected")) {
      this.setState({
        home: "",
        galery: "selected",
        isOpen: false,
      });
    }
  }

  render() {
    return (
      <nav className="nav">
        <Link
          to="/"
          onClick={() =>
            this.setState({
              galery: "",
              home: "selected",
              isOpen: false,
            })
          }
        >
          <div>
            <img
              src={require("../../Assets/Images/misc/logo.svg")}
              alt="logo.svg"
            />
          </div>
        </Link>
        <ul className={"nav-links" + (this.state.isOpen ? " nav-active" : "")}>
          <Link
            to="/"
            onClick={() =>
              this.setState({
                home: "selected",
                galery: "",
                isOpen: false,
              })
            }
          >
            <li className={this.state.home}>Home</li>
          </Link>
          <Link
            to="/Galery"
            onClick={() =>
              this.setState({
                home: "",
                galery: "selected",
                isOpen: false,
              })
            }
          >
            <li className={this.state.galery}>Galery</li>
          </Link>
        </ul>

        <div
          className={"burger" + (this.state.isOpen ? " switch" : "")}
          onClick={() => this.onClickHandler()}
        >
          <div className="line1"></div>
          <div className="line2"></div>
          <div className="line3"></div>
        </div>
      </nav>
    );
  }
}

export default Nav;
