import React from "react";
import images from "./Data";

class Slide extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      images: images,
      mounted: false,
    };
  }

  componentDidMount() {
    this.setState({ mounted: true });
  }

  componentWillUnmount() {
    this.setState({ mounted: false });
  }

  render() {
    return this.state.images.map((image, index) => (
      <div
        key={index}
        className={
          index === this.props.activeIndex ? "active center" : "inactive center"
        }
        style={this.state.style}
      >
        <div className="cards">
          <div className="img-cont">
            <div className="overlay">
              <p>{image.modelName}</p>
              <p>{image.text}</p>
            </div>
            <img src={image.src} alt={image.alt} width="600px" height="400px" />
          </div>
        </div>
      </div>
    ));
  }
}

export default Slide;
