import React from "react";
import "./Galery.scss";
import Slider from "./Slider/Slider.js";
import GaleryData from "./Data/GaleryData";
import GaleryData2 from "./Data/GaleryData2.js";
import GaleryData3 from "./Data/GaleryData3.js";

class Galery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      galery1: GaleryData,
      galery2: GaleryData2,
      galery3: GaleryData3,
    };
  }

  render() {
    return (
      <div className="Galery">
        <div className="container">
          <div className="Header">
            <h1>Recent Top</h1>
          </div>
          <div className="row">
            <div className="col-sm-12">
              <Slider />
            </div>
          </div>
        </div>
        <div className="container-fluid">
          <div className="Header">
            <h1>Gallery</h1>
          </div>
          <div className="galery-images-container">
            <div className="row justify-content-center">
              <div className="col-sm-6 col-md-6 col-lg-4">
                {this.state.galery1.map((image, index) => (
                  <div key={index} className="galery-image">
                    <div className="overlay">
                      <p>{image.modelName}</p>
                      <p>{image.text}</p>
                    </div>
                    <img
                      object-fit="cover"
                      src={image.src}
                      alt={image.alt}
                    ></img>
                  </div>
                ))}
              </div>
              <div className="col-sm-6 col-md-6 col-lg-4">
                {this.state.galery2.map((image, index) => (
                  <div key={index} className="galery-image">
                    <div className="overlay">
                      <p>{image.modelName}</p>
                      <p>{image.text}</p>
                    </div>
                    <img
                      object-fit="cover"
                      src={image.src}
                      alt={image.alt}
                    ></img>
                  </div>
                ))}
              </div>
              <div className="col-sm-6 col-md-6 col-lg-4">
                {this.state.galery3.map((image, index) => (
                  <div key={index} className="galery-image">
                    <div className="overlay">
                      <p>{image.modelName}</p>
                      <p>{image.text}</p>
                    </div>
                    <img
                      object-fit="cover"
                      src={image.src}
                      alt={image.alt}
                    ></img>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Galery;
