import React from "react";
import Slide from "./Slide.js";
import images from "./Data";
import "./Slider.scss";

class Slider extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeIndex: 0,
      length: images.length,
      mounted: false,
    };
  }

  componentDidMount() {
    this.setState({ mounted: true });
  }

  nextSlide(e) {
    let index = this.state.activeIndex;
    let length = this.state.length;

    if (index === length - 1) {
      index = 0;
    } else {
      index++;
    }

    this.setState({ activeIndex: index });
  }

  prevSlide(e) {
    let index = this.state.activeIndex;
    let length = this.state.length;

    if (index < 1) {
      index = length - 1;
    } else {
      index--;
    }
    this.setState({ activeIndex: index });
  }

  render() {
    return (
      <div className="Slider">
        <div className="arrow_left" onClick={(e) => this.prevSlide(e)}>
          <img
            className="arrow"
            src={require("../../../Assets/Images/misc/arrow_left.svg")}
            alt="arrow_left"
          ></img>
        </div>
        <Slide activeIndex={this.state.activeIndex} />
        <div className="arrow_right" onClick={(e) => this.nextSlide(e)}>
          <img
            className="arrow"
            src={require("../../../Assets/Images/misc/arrow_right.svg")}
            alt="arrow_right"
          ></img>
        </div>
      </div>
    );
  }
}

export default Slider;
